.login-page {
  width: 100%;
  max-width: 550px;
  min-height: 60vh;
  display: flex;
  align-items: center;
  margin-inline: auto;
  padding-inline: 4%;
  padding-block: 48px;
}
.login-page .login-form {
  width: 100%;
}
.login-page .login-form .input-group {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 24px;
}
.login-page .login-form .input-group label {
  font-size: 14px;
  color: #3d3d3d;
}
.login-page .login-form .input-group input {
  padding: 6px 12px;
  width: 100%;
  border-radius: 4px;
  border: 1px solid var(--background-color);
  background-color: #fafafa;
  box-shadow: 0px 1px 8px 0px rgba(47, 47, 47, 0.1607843137);
  outline: none;
  font-size: 12px;
  text-indent: 24px;
  padding-inline-end: 36px;
}
.login-page .login-form .input-group input:focus {
  border: 1px solid var(--main-color);
  background-color: var(--background-color);
}
.login-page .login-form .input-group input:active {
  border: 1px solid var(--main-color);
  background-color: var(--background-color);
}
.login-page .login-form .input-group .icon {
  position: absolute;
  bottom: -20px;
  right: 8px;
  transform: translateY(-50%);
}
.login-page .login-form .input-group .icon svg {
  width: 24px;
  height: 24px;
}
.login-page .login-form .input-group .eye-btn {
  position: absolute;
  bottom: -20px;
  left: 8px;
  transform: translateY(-50%);
}
.login-page .login-form button {
  cursor: pointer;
  margin-top: 32px;
  width: 100%;
  background-color: var(--main-color);
  color: var(--background-color);
  padding: 8px 12px;
  border-radius: 6px;
  text-align: center;
  border: none;
}/*# sourceMappingURL=Login.css.map */