.login-page {
  width: 100%;
  max-width: 550px;
  min-height: 60vh;
  display: flex;
  align-items: center;
  margin-inline: auto;
  padding-inline: 4%;
  padding-block: 48px;
  .login-form {
    width: 100%;

    .input-group {
      position: relative;
      display: flex;
      flex-direction: column;
      gap: 12px;
      margin-top: 24px;
      label {
        font-size: 14px;
        color: #3d3d3d;
      }
      input {
        padding: 6px 12px;
        width: 100%;
        border-radius: 4px;
        border: 1px solid var(--background-color);
        background-color: #fafafa;
        box-shadow: 0px 1px 8px 0px #2f2f2f29;
        outline: none;
        font-size: 12px;
        text-indent: 24px;
        padding-inline-end: 36px;

        &:focus {
          border: 1px solid var(--main-color);
          background-color: var(--background-color);
        }
        &:active {
          border: 1px solid var(--main-color);
          background-color: var(--background-color);
        }
      }
      .icon {
        position: absolute;
        bottom: -20px;
        right: 8px;
        transform: translateY(-50%);
        svg{
          width: 24px;
          height: 24px;
          
        }
      }
      .eye-btn {
        position: absolute;
        bottom: -20px;
        left: 8px;
        transform: translateY(-50%);
      }
    }
    button {
      cursor: pointer;
      margin-top: 32px;
      width: 100%;
      background-color: var(--main-color);
      color: var(--background-color);
      padding: 8px 12px;
      border-radius: 6px;
      text-align: center;
      border: none;
    }
  }
}
